import React, { Component } from "react";
import { render } from "react-dom";
import "./style.css";
import { TextBox } from "./TextBox";
import { Label } from "./Label";
import { Button } from "./Button";

class App extends Component {

  render() {
    return (
      <div class="container">
        <h1> My Atos </h1>
        <form action="action_page.php">
          <Label forname="empname" name="Employee name" />
          <TextBox type="text" name="empname" id="empname"/>
          <Label forname="email" name="Email" />
          <TextBox type="email" name="email" id="email" />
          <Label forname="empid" name="Employee ID" />
          <TextBox type="number" pattern="[5]{1}[0-9]{6}" name="number" id="empid" />
          <Label forname="number1" name="Phone number" />
          <TextBox type="tel" pattern="[7-9]{1}-[0-9]{4}-[0-9]{5}" name="tel" id="number1" />
          <Button />
        </form>
      </div>
    );
  }
}

render(<App />, document.getElementById("root"));
